"use client"

import { useSession, signOut } from "next-auth/react"
import { useRouter } from "next/navigation"
import { useEffect } from "react"
import { Truck, LogOut } from "lucide-react"
import { PageLoader } from "@/components/ui"

export default function DeliveryLayout({
  children,
}: {
  children: React.ReactNode
}) {
  const { data: session, status } = useSession()
  const router = useRouter()

  useEffect(() => {
    if (status === "unauthenticated") {
      router.push("/login/delivery")
    }
  }, [status, router])

  if (status === "loading") {
    return <PageLoader />
  }

  if (!session || session.user.role !== "delivery") {
    return <PageLoader />
  }

  return (
    <div className="min-h-screen bg-background">
      {/* Header */}
      <header className="sticky top-0 z-50 bg-primary-600 text-white safe-area-top">
        <div className="flex items-center justify-between px-4 h-14">
          <div className="flex items-center gap-3">
            <div className="h-8 w-8 bg-white/20 rounded-lg flex items-center justify-center">
              <Truck className="h-5 w-5" />
            </div>
            <div>
              <p className="font-semibold text-sm">Maa Ilay</p>
              <p className="text-xs text-primary-100">{session.user.name}</p>
            </div>
          </div>
          <button
            onClick={() => signOut({ callbackUrl: "/login/delivery" })}
            className="h-8 w-8 flex items-center justify-center rounded-lg hover:bg-white/10"
          >
            <LogOut className="h-5 w-5" />
          </button>
        </div>
      </header>

      {children}
    </div>
  )
}
